.registration {
  padding-top: 25px;
  text-align: center;
  @include fonts(18px, 500, 1.25);
  color: var(--default-text-color);

  &__container {
    display: flex;
    flex-direction: column;
    background: #fffff9;
    box-shadow: var(--registration_box-shadow);
    border-top-left-radius: 150px;
    border-top-right-radius: 150px;

    padding-left: 15px;
    padding-right: 15px;
    padding-top: 80px;
    padding-bottom: 40px;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: 350px) and (max-width: 767.99px) {
      border-top-left-radius: 50%;
      border-top-right-radius: 50%;
    }

    @media screen and (min-width: 767.98px) {
      flex-direction: row;
      justify-content: center;
      border-radius: 150px;
      padding-top: 35px;
      padding-bottom: 35px;
      padding-left: 35px;
      padding-right: 21px;
      margin-top: 10px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 8px;
    min-width: 114px;

    @media screen and (min-width: 767.98px) {
      align-items: flex-end;
      margin-right: 55px;
    }

    @media screen and (min-width: 1199.98px) {
      font-size: 20px;
      line-height: 1.2;
    }
  }

  &__item:last-child {
    margin-top: 30px;
  }
}

@media screen and (max-width: 767px) {
  .registration__title--position {
    position: relative;
    width: 200px;
    margin-left: auto;
    margin-right: auto;
  }

  .registration__title {
    position: absolute;
    top: 10px;
    left: 24%;
  }
}

.form {
  @media screen and (min-width: 767.98px) {
    & {
      display: flex;
    }
  }

  &__container {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
    margin-bottom: 35px;

    @media screen and (min-width: 767.98px) {
      width: 234px;
      margin-right: 25px;
      padding-top: 20px;
      padding-bottom: 20px;
      margin-bottom: 0;
      margin-top: 0;
    }

    &::before {
      content: "";
      display: inline-block;
      width: 100%;
      height: 1px;
      background-color: var(--placeholder-color-text);
      margin-bottom: 14px;

      @media screen and (min-width: 767.98px) {
        position: absolute;
        top: 0;
        left: -30px;
        height: 100%;
        width: 1px;
        margin-right: 30px;
        margin-bottom: 0;
      }
    }
  }

  &__subhead {
    @include fonts(16px, normal, 1.25);
    margin-bottom: 17px;
    text-align: center;

    @media screen and (min-width: 767.98px) {
      text-align: start;
      padding-right: 15px;

      &--center {
        text-align: center;
      }
    }
    @media screen and (min-width: 1199.98px) {
      font-size: 18px;
      line-height: 1.22;
    }
  }

  &__input {
    font-weight: normal;
    font-size: 16px;
    line-height: inherit;
    border: none;
    padding: 0;
    border-bottom: 1px solid var(--primery-accent-color);
    margin-bottom: 25px;
    background-color: transparent;
    transition: outline var(--animation), border-bottom-color var(--animation);

    @media screen and (min-width: 1199.98px) {
      font-size: 18px;
    }

    &:last-child {
      margin-bottom: 0;
    }
    &:focus {
      outline: 1px solid var(--primery-accent-color);
      border-bottom-color: var(--secondary-accent-col);
    }

    &::placeholder {
      color: var(--placeholder-color-text);
    }

    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus {
      -webkit-text-fill-color: var(--default-text-color);
      box-shadow: 0 0 0px 1000px rgba(254, 243, 228, 0.9) inset;
    }

    &:valid:not(:focus) {
      color: var(--default-text-color);
    }

    &:invalid:not(:focus) {
      color: red;
    }
  }
}
